import isNil from 'lodash'

export default {
  data() {
    return {
      //data here
    };
  },
  computed: {
    devMode() {
      return this.urlParam("hf_dev", "true");
    },
  },
  methods: {
    clickTracking(product_id, data) {
      var publicKey = this.devMode
        ? process.env.VUE_APP_EXIT_INTENT_PUBLIC_KEY
        : data.publicKey;
      var content_id = this.devMode
        ? process.env.VUE_APP_EXIT_INTENT_BLOCK_ID
        : data.content.id;

      var payload = {
        product_id: product_id,
        content_id: content_id,
        visitor: data.visitor,
        from_us: true,
        callback: "callBackHfClickTracking",
      };

      payload = encodeURI(JSON.stringify(payload));

      var url = process.env.VUE_APP_API + "tracker/click";

      url = url + "?public_key=" + publicKey + "&payload=" + payload;

      var script = document.createElement("script");
      script.type = "text/javascript";
      script.async = true;
      script.id = "hf_clickTracking";
      script.src = url;
      document.getElementsByTagName("head")[0].appendChild(script);
    },
    goToUrl(url) {
      top.window.location.href = url;
    },
    urlParam(param, value) {
      var queryString = window.location.search;
      var urlParams = new URLSearchParams(queryString);
      var paramValue = urlParams.get(param);
      return paramValue === value;
    },
    getUrlParam(param) {
      var queryString = window.location.search;
      var urlParams = new URLSearchParams(queryString);
      var paramValue = urlParams.get(param);
      return paramValue;
    },
    switchImage(event) {
      var image = event.target;
      var image_switch = image.getAttribute("data-img-switch");
      var src = image.getAttribute("src");
      image.setAttribute("data-img-switch", src);
      image.src = image_switch;
    },
    isNil(value) {
      return isNil(value)
    }
  },
  mounted() {
    /**/
  },
};
