<template>
  <div v-if="loaded" class="rec-grid">
    <div class="hintify-root">
      <b-button class="rec-grid-trigger-button" v-if="enableButton" @click="handleClick()"
        >Rec Grid</b-button
      >
      <b-sidebar
        id="RecGridSideBar"
        shadow
        backdrop
        width="30em"
        v-model="sidebarVisible"
        @hidden="closeWidget"
      >
        <template v-slot:header>
          <div class="rec-grid-header">
            <button
              v-b-toggle.RecGridSideBar
              type="button"
              aria-label="Close"
              class="close text-dark"
            >
              <svg
                viewBox="0 0 16 16"
                width="1em"
                height="1em"
                focusable="false"
                role="img"
                aria-label="x"
                xmlns="http://www.w3.org/2000/svg"
                fill="currentColor"
                class="bi-x b-icon bi"
              >
                <g>
                  <path
                    d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"
                  ></path>
                </g>
              </svg>
            </button>
            <div class="main-text">
              <h1>
                Selecionamos
                <br />
                para você
              </h1>
            </div>
            <b-img
              class="header-img"
              src="https://picsum.photos/500/500/?image=54"
              fluid
            ></b-img>
          </div>
        </template>
        <div class="rec-grid-body">
          <div class="rec-grid-tabs">
            <b-tabs card pills content-class="mt-3" align="center">
              <b-tab title="Recomendados" active>
                <rec-grid-content :mainData="mainData" :products="personalRecommendation"></rec-grid-content>
              </b-tab>
              <b-tab title="Últimos Vistos">
                <rec-grid-content :mainData="mainData" :products="bestSellers"></rec-grid-content>
              </b-tab>
              <b-tab title="Os mais vendidos">
                <rec-grid-content :mainData="mainData" :products="visitorHistory"></rec-grid-content>
              </b-tab>
            </b-tabs>
          </div>
        </div>
      </b-sidebar>
    </div>
  </div>
</template>
<script>
import RecGridContent from "./RecGridContent";
export default {
  name: "RecGrid",
  components: {
    RecGridContent,
  },
  props: {
    mainData: Object,
    outsideWidgetTrigger: Boolean,
    enableButton: Boolean,
  },
  computed: {
    personalRecommendation() {
      var result = null;
      if (this.mainData.fullResult.personal_recommendation.data_ids.length) {
        result = this.mainData.fullResult.personal_recommendation.data;
      } else {
        result = this.mainData.fullResult.best_sellers.data;
      }
      return result;
    },
    bestSellers() {
      return this.mainData.fullResult.best_sellers.data;
    },
    visitorHistory() {
      return this.mainData.fullResult.visitor_history.data;
    },
  },
  data() {
    return {
      loaded: false,
      visible: false,
      name: "",
      source: null,
      backgroundColor: "#FFF",
      borderColor: "#DDD",
      sidebarVisible: false,
    };
  },
  methods: {
    showValue() {},
    closeWidget() {
      this.$emit("close");
    },
    showModal() {
      this.loaded = true;
      setTimeout(() => {
        this.slickLoaded = true;
      }, 400);
    },
    handleClick() {
      this.sidebarVisible = true;
    },
  },
  mounted() {
    this.showModal();
  },
  watch: {
    outsideWidgetTrigger: function (newVal) {
      this.sidebarVisible = newVal;
    },
  },
};
</script>
