<template>
  <div class="hintify-base-widget">
    <div v-if="canShowWidget">
      <div v-if="showBackdrop" id="hintify-widget-backdrop"></div>
      <div class="main" @click.self="hideWidget('backdrop')">
        <exit-intent-casa-das-correias
          v-if="
            widget == 'exit-intent' && mainData.publicKey == publicKeys.casadascorreias
          "
          @close="hideWidget"
          :mainData="mainData"
          :widgetConfig="widgetConfig"
        >
        </exit-intent-casa-das-correias>

        <exit-intent
          v-else-if="widget == 'exit-intent'"
          @close="hideWidget"
          :mainData="mainData"
          :widgetConfig="widgetConfig"
        >
        </exit-intent>

        <rec-grid
          v-if="widget == 'rec-grid'"
          @close="hideWidget"
          :mainData="mainData"
          :outsideWidgetTrigger="outsideWidgetTrigger"
          :enableButton="enableTriggerButton"
          :widgetConfig="widgetConfig"
        >
        </rec-grid>

        <bottom-slider
          v-if="widget == 'bottom-slider'"
          @close="hideWidget"
          :mainData="mainData"
          :widgetConfig="widgetConfig"
        >
        </bottom-slider>

        <live-search
          v-if="widget == 'live-search'"
          @close="hideWidget"
          :mainData="mainData"
          :outsideWidgetTrigger="outsideWidgetTrigger"
          :enableButton="enableTriggerButton"
          :loadingData="loadingData"
          :loadingMoreData="loadingMoreData"
          :widgetConfig="widgetConfig"
          @loadSearchResult="loadData"
        >
        </live-search>

        <full-live-search
          v-else-if="
            widget == 'full-live-search' &&
            mainData.publicKey == publicKeys.artilheirojoias
          "
          @close="hideWidget"
          :mainData="mainData"
          :extSearchQuery="searchQuery"
          :outsideWidgetTrigger="outsideWidgetTrigger"
          :enableButton="enableTriggerButton"
          :loadingData="loadingData"
          :loadingMoreData="loadingMoreData"
          :slideUpEffectOn="false"
          :widgetConfig="widgetConfig"
          :customClasse="'artilheiro-joias'"
          @loadSearchResult="loadData"
        >
        </full-live-search>

        <full-live-search
          v-else-if="
            widget == 'full-live-search' && mainData.publicKey == publicKeys.florattajoias
          "
          @close="hideWidget"
          :mainData="mainData"
          :extSearchQuery="searchQuery"
          :outsideWidgetTrigger="outsideWidgetTrigger"
          :enableButton="enableTriggerButton"
          :loadingData="loadingData"
          :loadingMoreData="loadingMoreData"
          :slideUpEffectOn="false"
          :customClasse="'floratta-joias'"
          :widgetConfig="widgetConfig"
          @loadSearchResult="loadData"
        >
        </full-live-search>

        <full-live-search
          v-else-if="widget == 'full-live-search'"
          @close="hideWidget"
          :mainData="mainData"
          :extSearchQuery="searchQuery"
          :outsideWidgetTrigger="outsideWidgetTrigger"
          :enableButton="enableTriggerButton"
          :loadingData="loadingData"
          :loadingMoreData="loadingMoreData"
          :slideUpEffectOn="false"
          :widgetConfig="widgetConfig"
          @loadSearchResult="loadData"
        >
        </full-live-search>
      </div>
    </div>
    <div v-else>
      <h2 class="base-widget-loading" v-if="devMode">loading...</h2>
      <h2 v-else>unauthorized</h2>
    </div>
  </div>
</template>
<script>
import ExitIntent from "@/components/ExitIntent";
import ExitIntentCasaDasCorreias from "@/components/ExitIntentCasaDasCorreias";
import BottomSlider from "@/components/BottomSlider";
import RecGrid from "@/components/RecGrid";
import LiveSearch from "./LiveSearch";
import FullLiveSearch from "./FullLiveSearch";

export default {
  name: "BaseWidget",
  components: {
    ExitIntent,
    ExitIntentCasaDasCorreias,
    RecGrid,
    BottomSlider,
    LiveSearch,
    FullLiveSearch,
  },
  computed: {
    canShowWidget() {
      return this.loaded && this.visible;
    },
    showBackdrop() {
      return this.urlParam("backdrop", "true");
    },
    backdropCloseEvent() {
      return this.urlParam("backdrop_close_event", "true");
    },
    widgetConfig() {
      return this.mainData.content && this.mainData.content.widget_config
        ? this.mainData.content.widget_config
        : this.defaultWidgetConfig;
    },
  },
  data() {
    return {
      publicKeys: {
        demo: "0003AEAA-3E4A-6EFA-3B96-1184B6D8C4A1",
        casadascorreias: "F9AF07E6-3AE5-5ED8-9E72-0A82C0CD9AD6",
        artilheirojoias: "147085AD-C081-C8F5-40AA-08F3E03DA1A3",
        florattajoias: "870B57D3-7454-D0A1-B77E-609A541AA2E2",
      },
      mainData: {
        visible: false,
        publicKey: "0003AEAA-3E4A-6EFA-3B96-1184B6D8C4A1", // demo
        blockId: null,
        visitor: null,
        products: [],
      },
      defaultWidgetConfig: {
        topImage: {
          url: "/media/exitintent_header.png",
        },
        buttonLoadMore: {
          title: "Mais Resultados",
          bgColor: "#000",
          color: "#fff",
        },
        buttonBuy: {
          title: "COMPRAR",
          bgColor: "#000",
          color: "#fff",
        },
        productName: {
          color: "#000",
        },
        productPrice: {
          enabled: true,
          color: "#000",
        },
        productSalePrice: {
          enabled: true,
          color: "#000",
        },
        installments: {
          enabled: true,
          extraText: "sem juros",
          color: "#000",
        },
        spotPrice: {
          enabled: true,
          extraText: "à vista com desconto",
          color: "#000",
        },
      },
      origin: null,
      loaded: false,
      visible: false,
      widget: null,
      loadingData: false,
      loadingMoreData: false,
      outsideWidgetTrigger: false,
      enableTriggerButton: false,
      backgroundColor: "#FFF",
      borderColor: "#DDD",
      searchQuery: "",
    };
  },
  methods: {
    showValue() {},
    hideWidget(origin = null) {
      // check if widget can be closed by backdrop event
      if (!this.backdropCloseEvent && origin === "backdrop") return false;
      // check if widget can be c
      this.outsideWidgetTrigger = false;
      this.mainData.visible = false;
      window.top.postMessage(this.mainData, this.origin);
    },
    validWidget(widget) {
      switch (widget) {
        case "exit-intent":
          return true;
        case "rec-grid":
          return true;
        case "bottom-slider":
          return true;
        case "live-search":
          return true;
        case "full-live-search":
          return true;
        default:
          return false;
      }
    },
    // Called sometime after postMessage is called
    receiveMessage(event) {
      // console.log('[iframe][receiveMessage] event: ', event)

      // Do we trust the sender of this message?
      if (event.origin !== this.source) {
        return false;
      }
      // event.source is window.opener
      // event.data is "hello there!"

      // Assuming you've verified the origin of the received message (which
      // you must do in any case), a convenient idiom for replying to a
      // message is to call postMessage on event.source and provide
      // event.origin as the targetOrigin.

      // Did Update - from outside
      if (event.data.outsideWidgetTrigger) {
        this.mainData.searchResultTop = event.data.searchResultTop || false;
        this.outsideWidgetTrigger = true;
        if (typeof event.data.searchQuery !== "undefined") {
          this.searchQuery = event.data.searchQuery;
        }
      }
      // Did Mount - First time trigger from outside
      else {
        this.mainData = event.data;
        this.origin = event.origin;
        if (typeof this.mainData !== "object") return false;
        if (!this.mainData.blockId) return false;
        if (typeof event.data.searchQuery !== "undefined") {
          this.searchQuery = event.data.searchQuery;
        }
        this.loadData(
          this.mainData.publicKey,
          this.mainData.blockId,
          this.mainData.visitor
        );
      }
    },
    loadDevMode() {
      this.mainData.publicKey = process.env.VUE_APP_PUBLIC_KEY;
      this.mainData.blockId = null;
      this.mainData.searchResultTop = 100;
      switch (this.getUrlParam("widget")) {
        case "exit-intent":
          this.mainData.blockId = process.env.VUE_APP_EXIT_INTENT_BLOCK_ID;
          break;
        case "bottom-slider":
          this.mainData.blockId = process.env.VUE_APP_BOTTOM_SLIDER_BLOCK_ID;
          break;
        case "rec-grid":
          this.mainData.blockId = process.env.VUE_APP_REC_GRID_BLOCK_ID;
          break;
        case "live-search":
          this.mainData.blockId = process.env.VUE_APP_LIVE_SEARCH_BLOCK_ID;
          this.outsideWidgetTrigger = true;
        case "full-live-search":
          this.mainData.blockId = process.env.VUE_APP_FULL_LIVE_SEARCH_BLOCK_ID;
          this.outsideWidgetTrigger = true;
          break;
      }
      this.mainData.visitor = process.env.VUE_APP_EXIT_INTENT_VISITOR_ID;
      this.enableTriggerButton = true;
      this.loadData(
        this.mainData.publicKey,
        this.mainData.blockId,
        this.mainData.visitor
      );
    },
    loadData(publicKey, blockId, visitor, contentParams = null, concat = false) {
      let payload = {
        block_id: blockId,
        content_params: contentParams,
        filter: null,
        visitor: visitor,
        callback: null,
        render: false,
      };

      payload = encodeURI(JSON.stringify(payload));

      const endpoint = "/on/load?public_key=" + publicKey + "&payload=" + payload;
      const widget = this.getUrlParam("widget");

      if (concat) {
        this.loadingMoreData = true;
      } else {
        this.loadingData = true;
      }

      this.$http.get(endpoint).then((response) => {
        const responseData = response.data;

        switch (widget) {
          case "rec-grid":
          case "exit-intent":
            if (
              responseData.personal_recommendation &&
              responseData.personal_recommendation.data_ids.length
            ) {
              this.mainData.products = responseData.personal_recommendation.data;
            } else {
              this.mainData.products = responseData.best_sellers.data;
            }
            break;
          case "live-search":
          case "full-live-search":
            if (concat) {
              this.mainData.products = [
                ...this.mainData.products,
                ...responseData.result.data,
              ];
            } else {
              this.mainData.products = responseData.result.data || [];
            }
            break;
        }

        this.mainData.fullResult = responseData;
        this.visible = true;
        this.mainData.visible = this.visible;

        if (concat) {
          this.loadingMoreData = false;
        } else {
          this.loadingData = false;
        }

        window.top.postMessage(this.mainData, this.origin);
      });
    },
    loadWidget() {
      var widget = this.getUrlParam("widget");
      this.widget = widget ? widget : this.widget;
      this.source = this.getUrlParam("source");
      this.source = this.source ? atob(this.source) : null;
      this.loaded = this.widget && this.validWidget(this.widget);

      if (this.devMode) {
        this.loadDevMode();
      }

      // event from parent postmessage
      window.addEventListener("message", this.receiveMessage, false);
    },
  },
  mounted() {
    this.loadWidget();
  },
};
</script>
