<template>
  <div v-if="loaded" :class="['full-live-search', customClasse]">
    <div class="widget-root" @click.self="closeWidget">
      <b-button
        class="full-live-search-trigger-button"
        v-if="enableButton && !visible"
        @click="handleClick()"
        >Live Search</b-button
      >
      <div v-if="enableButton && visible" class="full-live-search-input-wrapper">
        <input
          id="searchInput"
          ref="searchInput"
          class="full-live-search-input"
          type="text"
          @input="handleSearchChange($event.target.value)"
          v-model="searchQuery"
          autocomplete="off"
          autofocus
        />
      </div>
      <modal
        ref="hfModal"
        v-if="visible"
        @close="closeWidget"
        :class="[
          {
            'slide-up-effect-on': slideUpEffectOn,
            'slide-up-effect-off': !slideUpEffectOn,
            show: searchResultsVisible,
          },
        ]"
        :style="{
          top: mainData.searchResultTop
            ? `${mainData.searchResultTop}px !important`
            : false,
          height: mainData.searchResultTop
            ? `calc(100% - ${mainData.searchResultTop}px) !important`
            : false,
        }"
      >
        <div slot="header">
          <div
            class="header-spacer"
            style="min-height: 10px; background: transparent; width: 100%"
          ></div>
          <div v-if="!loadingData && mainData.products.length" class="header-result-box">
            <h5 v-if="mainData.products.length > 1">
              Foram encontrados <span>{{ mainData.products.length }}</span> produtos
            </h5>
            <h5 v-else>
              Foi encontrado <span>{{ mainData.products.length }}</span> produto
            </h5>
          </div>
          <button
            v-if="visible"
            type="button"
            class="btn btn-close-full-live-search"
            @click="closeWidget"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body-content">
          <b-overlay
            :show="loadingData"
            spinner-variant="primary"
            spinner-type="grow"
            spinner-small
            rounded="sm"
            variant=""
            style="min-height: 400px"
          >
            <div v-if="!loadingData && mainData.products.length" class="hintify-root">
              <div
                class="hintify-item"
                v-for="item in mainData.products"
                :key="item.id"
                @click="clickTracking(item.id, mainData)"
              >
                <div class="hintify-item-wrapper">
                  <div class="hintify-image">
                    <a :title="item.name" @click="goToUrl(item.url)">
                      <img
                        class="hintify-image-front"
                        :src="item.image"
                        :data-img-switch="item.image2"
                        @mouseenter="switchImage($event)"
                        @mouseleave="switchImage($event)"
                      />
                    </a>
                  </div>
                  <div class="hintify-item-content">
                    <a :title="item.name" @click="goToUrl(item.url)">
                      <div class="hintify-details">
                        <div
                          class="hintify-name"
                          :style="{
                            color: widgetConfig.productName.color || false,
                          }"
                        >
                          {{ item.name }}
                        </div>
                        <div
                          class="hintify-price-wrapper"
                          v-if="widgetConfig.productPrice.enabled"
                        >
                          <div
                            v-if="item.discount"
                            class="hintify-price"
                            :style="{
                              color: widgetConfig.productPrice.color || false,
                            }"
                          >
                            R$ {{ item.price }}
                          </div>
                          <div v-else class="hintify-price"></div>
                        </div>
                        <div
                          class="hintify-sale-price"
                          v-if="widgetConfig.productSalePrice.enabled"
                          :style="{
                            color: widgetConfig.productSalePrice.color || false,
                          }"
                        >
                          R$ {{ item.sale_price }}
                        </div>
                        <div
                          v-if="
                            widgetConfig.spotPrice.enabled &&
                            (item.spot_price || item.boleto_price)
                          "
                          class="hintify-boleto-price"
                          :style="{
                            color: widgetConfig.spotPrice.color || false,
                          }"
                        >
                          <span class="hintify-boleto-price-big">
                            R$ {{ item.spot_price || item.boleto_price }}
                          </span>
                          <span>{{ widgetConfig.spotPrice.extraText || "" }}</span>
                        </div>
                        <div
                          class="hintify-installments-wrapper"
                          v-if="widgetConfig.installments.enabled"
                        >
                          <div
                            v-if="item.installments"
                            class="hintify-installments"
                            :style="{
                              color: widgetConfig.installments.color || false,
                            }"
                          >
                            <span class="hintify-installments-big">
                              {{ item.mo_payx }}x
                            </span>
                            <span>de</span>
                            <span class="hintify-installments-big">
                              R$ {{ item.mo_payv }}
                            </span>
                            <span>{{ widgetConfig.installments.extraText || "" }}</span>
                          </div>
                          <div
                            v-else
                            class="hintify-installments"
                            :style="{
                              color: widgetConfig.installments.color || false,
                            }"
                          >
                            <span> Ou 1x </span>
                            <span> de </span>
                            <span class="hintify-installments-big"
                              >R$ {{ item.sale_price }}
                            </span>
                            <span>{{ widgetConfig.installments.extraText || "" }}</span>
                          </div>
                        </div>
                      </div>
                    </a>
                  </div>
                  <div class="hintify-btn-box">
                    <div
                      class="hintify-btnbuy"
                      @click="goToUrl(item.url)"
                      :style="{
                        background: widgetConfig.buttonBuy.bgColor || false,
                        color: widgetConfig.buttonBuy.color || false,
                      }"
                    >
                      {{ widgetConfig.buttonBuy.title }}
                    </div>
                  </div>
                </div>
              </div>
              <div class="load-more-wrapper">
                <b-button
                  v-if="loadingMoreData"
                  class="full-live-search-load-more-button"
                  :style="{
                    background: widgetConfig.buttonLoadMore.bgColor || false,
                    color: widgetConfig.buttonLoadMore.color || false,
                  }"
                >
                  <b-spinner small label="Small Spinner"></b-spinner>
                </b-button>
                <b-button
                  v-else
                  class="full-live-search-load-more-button"
                  @click="loadMoreProducts()"
                  :style="{
                    background: widgetConfig.buttonLoadMore.bgColor || false,
                    color: widgetConfig.buttonLoadMore.color || false,
                  }"
                >
                  {{ widgetConfig.buttonLoadMore.title }}
                </b-button>
              </div>
            </div>
            <div
              v-if="
                !loadingData && searchQuery.length > 1 && !this.mainData.products.length
              "
              class="empty-products"
              style="padding: 30px; margin-top: 5%"
            >
              <h5>Nenhum resultado encontrado...</h5>
            </div>
          </b-overlay>
        </div>
        <div slot="footer"></div>
      </modal>
    </div>
  </div>
</template>
<script>
import Modal from "./Modal";

export default {
  name: "FullLiveSearch",
  components: {
    Modal,
  },
  props: {
    mainData: Object,
    extSearchQuery: String,
    outsideWidgetTrigger: Boolean,
    loadingData: Boolean,
    loadingMoreData: Boolean,
    enableButton: Boolean,
    slideUpEffectOn: {
      type: Boolean,
      default: false,
    },
    customClasse: {
      type: String,
      default: "default",
    },
    widgetConfig: Object,
  },
  data() {
    return {
      loaded: false,
      visible: false,
      searchResultsVisible: false,
      name: "",
      source: null,
      backgroundColor: "#FFF",
      borderColor: "#DDD",
      sidebarVisible: false,
      searchQuery: "",
      searchInterval: null,
      minSearchChars: 2,
    };
  },
  methods: {
    showValue() {},
    closeWidget() {
      this.$emit("close");
      this.visible = false;
    },
    showModal() {
      this.loaded = true;
    },
    handleClick() {
      this.visible = !this.visible;
      if (this.visible) {
        this.$nextTick(() => {
          this.$refs.searchInput.focus();
        });
      }
    },
    handleSearchChange(query) {
      if (typeof query === "undefined") return false;

      if (typeof this.searchInterval === "number") {
        clearTimeout(this.searchInterval);
      }

      this.searchInterval = setInterval(() => {
        if (query.length >= this.minSearchChars) {
          const contentParams = {
            query: query,
          };
          this.$emit(
            "loadSearchResult",
            this.mainData.publicKey,
            this.mainData.blockId,
            this.mainData.visitor,
            contentParams
          );
          this.searchQuery = query;
        }
        this.searchResultsVisible = query.length > 1;
        this.scrollToTop();
        clearTimeout(this.searchInterval);
      }, 300);
    },
    loadMoreProducts() {
      const excludeList = this.excludeList();
      const contentParams = {
        query: this.searchQuery,
        exclude: excludeList,
      };
      this.$emit(
        "loadSearchResult",
        this.mainData.publicKey,
        this.mainData.blockId,
        this.mainData.visitor,
        contentParams,
        true
      );
    },
    excludeList() {
      const excludeList = this.mainData.products
        ? this.mainData.products.map((e) => e.id)
        : [];
      return excludeList;
    },
    scrollToTop() {
      document.querySelector(".hf-modal").scrollTop = 0;
    },
  },
  mounted() {
    this.showModal();
  },
  watch: {
    outsideWidgetTrigger: function (newVal) {
      this.visible = newVal;
    },
    extSearchQuery: function (newVal) {
      this.handleSearchChange(newVal);
    },
  },
};
</script>
