<template>
    <div v-if="modalShow" class="hf-modal">
        <div class="hf-modal-header">
            <slot name="header">
                This is the default tile!
                <button
                    type="button"
                    class="btn btn-close"
                    @click="close">
                    <span aria-hidden="true">&times;</span>
                </button>
            </slot>
        </div>

        <div class="hf-modal-body">
            <slot>
                Default Body
            </slot>
        </div>

        <div class="hf-modal-footer">
            <slot name="footer">
                I'm the default footer!

                <button
                    type="button"
                    class="btn-green"
                    @click="close"
                >
                    Close me!
                </button>
            </slot>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Modal',
    data() {
        return {
            modalShow: false
        }
    },
    methods: {
        close() {
            // inside hintify we do not need to close modal
            //this.modalShow = false;
            this.$emit('close');
        },
    },
    mounted() {
        this.modalShow = true;
    }
}
</script>