            <img
              class="hintify-image-front"
              :src="item.image"
              :data-img-switch="item.image2"
              @mouseenter="switchImage($event)"
              @mouseleave="switchImage($event)"
            />
          </a>
        </div>
        <div v-if="false" class="hintify-item-content">
          <a :title="item.name" @click="goToUrl(item.url)">
            <div class="hintify-details">
              <div class="hintify-name">{{ item.name }}</div>
              <div class="hintify-sale-price">R$ {{ item.sale_price }}</div>
            </div>
          </a>
          <div class="hintify-btn-box">
            <div class="hintify-btnbuy" @click="goToUrl(item.url)">COMPRAR</div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div v-else>
    <h3>Sem produtos no momento</h3>
  </div>
</template>
<script>
export default {
  name: "RecGridContent",
  computed: {
    productsFound() {
      return this.products.length > 0;
    },
  },
  props: {
    mainData: Object,
    products: Array,
  },
};
</script>
