<template>
  <div v-if="loaded" class="exit-intent">
    <modal :class="{ invisible: !slickLoaded }" @close="closeModal">
      <div slot="header">
        <div class="exit-intent-header-wrapper">
          <img class="exit-intent-header-image" :src="widgetConfig.topImage.url" />
        </div>
        <div class="close-area" @click="closeModal">
          <button type="button" class="btn btn-close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
      </div>
      <div class="modal-body-content">
        <VueSlickCarousel class="hintify-root" v-bind="sliderSettings">
          <div
            class="hintify-item"
            v-for="item in mainData.products"
            :key="item.id"
            @click="clickTracking(item.id, mainData)"
          >
            <div class="hintify-item-wrapper">
              <div class="hintify-image">
                <a :title="item.name" @click="goToUrl(item.url)">
                  <img
                    class="hintify-image-front"
                    :src="item.image"
                    :data-img-switch="item.image2"
                    @mouseenter="switchImage($event)"
                    @mouseleave="switchImage($event)"
                  />
                </a>
              </div>
              <div class="hintify-item-content">
                <a :title="item.name" @click="goToUrl(item.url)">
                  <div class="hintify-details">
                    <div 
                      class="hintify-name"
                      :style="{                    
                        color: widgetConfig.productName.color || false
                      }"
                    >
                        {{ item.name }}
                    </div>
                    <div 
                      class="hintify-sale-price"
                      :style="{                    
                        color: widgetConfig.productPrice.color || false
                      }"
                    >
                      R$ {{ item.sale_price }}
                    </div>
                  </div>
                </a>
              </div>
              <div class="hintify-btn-box">
                <div 
                  class="hintify-btnbuy" 
                  @click="goToUrl(item.url)"
                  :style="{                    
                    background: widgetConfig.buttonBuy.bgColor || false,
                    color: widgetConfig.buttonBuy.color || false
                  }"
                >
                  {{ widgetConfig.buttonBuy.title }}
                </div>
              </div>
            </div>
          </div>
        </VueSlickCarousel>
      </div>
      <div slot="footer"></div>
    </modal>
  </div>
</template>
<script>
import Modal from "./Modal";

export default {
  name: "ExitIntent",
  components: {
    Modal,
  },
  props: {
    mainData: Object,
    widgetConfig: Object    
  },
  data() {
    return {
      loaded: false,
      visible: false,
      slickLoaded: false,
      name: "",
      source: null,
      backgroundColor: "#FFF",
      borderColor: "#DDD",
      sliderSettings: {
        dots: false,
        draggable: false,
        focusOnSelect: false,
        centerMode: false,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 3,
        touchThreshold: 5,
      }    
    };
  },
  methods: {
    showValue() {},
    closeModal() {
      this.$emit("close");
    },
    showModal() {
      this.loaded = true;
      setTimeout(() => {
        this.slickLoaded = true;
      }, 400);
    },
    switchImage(event) {
      var image = event.target;
      var image_switch = image.getAttribute("data-img-switch");
      var src = image.getAttribute("src");
      image.setAttribute("data-img-switch", src);
      image.src = image_switch;
    },
  },
  mounted() {
    this.showModal();
  },
};
</script>
