<template>
  <div v-if="loaded" class="live-search">
    <div class="widget-root" @click.self="closeWidget">
      <b-button
        class="live-search-trigger-button"
        v-if="enableButton && !visible"
        @click="handleClick()"
        >Live Search</b-button
      >
      <div v-if="visible" class="live-search-input-wrapper">
        <input
          id="searchInput"
          ref="searchInput"
          class="live-search-input"
          type="text"
          @input="handleSearchChange($event)"
          v-model="searchQuery"
          autocomplete="off"
          autofocus
        />
      </div>
      <button v-if="visible" type="button" class="btn btn-close" @click="closeWidget">
        <span aria-hidden="true">Fechar</span>
      </button>
      <modal
        v-if="visible"
        @close="closeWidget"
        :class="['modal-p-bottom', { show: searchResultsVisible }]"
      >
        <div slot="header">
          <div
            class="header-spacer"
            style="min-height: 10px; background: #ffffff; width: 100%"
          ></div>
        </div>
        <div class="modal-body-content">
          <b-overlay
            :show="loadingData"
            spinner-variant="primary"
            spinner-type="grow"
            spinner-small
            rounded="sm"
            variant=""
            style="min-height: 400px"
          >
            <div v-if="!loadingData && mainData.products.length" class="hintify-root">
              <div
                class="hintify-item"
                v-for="item in mainData.products"
                :key="item.id"
                @click="clickTracking(item.id, mainData)"
              >
                <div class="hintify-item-wrapper">
                  <div class="hintify-image">
                    <a :title="item.name" @click="goToUrl(item.url)">
                      <img
                        class="hintify-image-front"
                        :src="item.image"
                        :data-img-switch="item.image2"
                        @mouseenter="switchImage($event)"
                        @mouseleave="switchImage($event)"
                      />
                    </a>
                  </div>
                  <div class="hintify-item-content">
                    <a :title="item.name" @click="goToUrl(item.url)">
                      <div class="hintify-details">
                        <div class="hintify-name">{{ item.name }}</div>
                        <div class="hintify-sale-price">R$ {{ item.sale_price }}</div>
                      </div>
                    </a>
                  </div>
                  <div class="hintify-btn-box">
                    <div class="hintify-btnbuy" @click="goToUrl(item.url)">COMPRAR</div>
                  </div>
                </div>
              </div>
              <div class="load-more-wrapper">
                <b-button v-if="loadingMoreData" class="live-search-load-more-button">
                  <b-spinner small label="Small Spinner"></b-spinner>
                </b-button>
                <b-button
                  v-else
                  class="live-search-load-more-button"
                  @click="loadMoreProducts()"
                  >Mais Resultados
                </b-button>
              </div>
            </div>
            <div
              v-if="
                !loadingData && searchQuery.length > 1 && !this.mainData.products.length
              "
              class="empty-products"
              style="padding: 30px; margin-top: 5%"
            >
              <h5>Nenhum resultado encontrado...</h5>
            </div>
          </b-overlay>
        </div>
        <div slot="footer"></div>
      </modal>
    </div>
  </div>
</template>
<script>
import Modal from "./Modal";

export default {
  name: "LiveSearch",
  components: {
    Modal,
  },
  props: {
    mainData: Object,
    outsideWidgetTrigger: Boolean,
    loadingData: Boolean,
    loadingMoreData: Boolean,
    enableButton: Boolean,
  },
  data() {
    return {
      loaded: false,
      visible: false,
      searchResultsVisible: false,
      name: "",
      source: null,
      backgroundColor: "#FFF",
      borderColor: "#DDD",
      sidebarVisible: false,
      searchQuery: "",
      searchInterval: null,
      minSearchChars: 2,
    };
  },
  methods: {
    showValue() {},
    closeWidget() {
      this.$emit("close");
      this.visible = false;
    },
    showModal() {
      this.loaded = true;
    },
    handleClick() {
      this.visible = !this.visible;
      if (this.visible) {
        this.$nextTick(() => {
          this.$refs.searchInput.focus();
        });
      }
    },
    handleSearchChange($event) {
      // necessary for android input changes
      this.searchQuery = $event.target.value

      if (typeof this.searchInterval === "number") {
        clearTimeout(this.searchInterval);
      }

      this.searchInterval = setInterval(() => {
        if (this.searchQuery.length >= this.minSearchChars) {
          const contentParams = {
            query: this.searchQuery,
          };
          this.$emit(
            "loadSearchResult",
            this.mainData.publicKey,
            this.mainData.blockId,
            this.mainData.visitor,
            contentParams
          );
        }
        this.searchResultsVisible = this.searchQuery.length > 1;
        clearTimeout(this.searchInterval);
      }, 300);
    },
    loadMoreProducts() {
      const excludeList = this.excludeList();
      const contentParams = {
        query: this.searchQuery,
        exclude: excludeList,
      };
      this.$emit(
        "loadSearchResult",
        this.mainData.publicKey,
        this.mainData.blockId,
        this.mainData.visitor,
        contentParams,
        true
      );
    },
    excludeList() {
      const excludeList = this.mainData.products
        ? this.mainData.products.map((e) => e.id)
        : [];
      return excludeList;
    },
  },
  mounted() {
    this.showModal();
  },
  watch: {
    outsideWidgetTrigger: function (newVal) {
      if (newVal) {
        this.$nextTick(() => {
          this.$refs.searchInput.focus();
        });
      }
      this.visible = newVal;
    },
  },
};
</script>
