          height="400px"
          style="border: none;position: fixed;display: block !important;z-index: 2147483647;left: 0px;top: 0px;width: 100%;height: 854px;">
      </iframe>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Test',
  data() {
    return {
      visible: false,
      linkWidget: ''
    }
  },
  methods: {
    loadFrame() {
      var baseUrl = window.location.origin;
      this.linkWidget = `${baseUrl}?${'widget=exit-intent'}`;
      this.visible = true;
    },
    hideWidget() {
      this.visible = false;
    }
  },
  mounted() {
    //...
  }
}
</script>
